* {
    font-family: Verdana, Geneva, Tahoma, sans-serif;
}

body {
    padding: 60px;
}

input{
    font-size: 1.3rem;
    width: 100%;
    padding: 10px;
    color: grey;
}

.card-grid {
    display: flex;
    flex-wrap: wrap;
}

.card{
    width: 300px;
    align-content: center;
    border-radius: 6px;
    border: 1px solid rgba(95, 95, 95, 0.3);
    box-shadow: 1px 1px 3px .5px rgba(0,0,0,.3);
    margin-bottom: 10px;
    margin-right: 10px;
    overflow: hidden;
    padding: .5rem;
}

.card p {
    padding: 5px;
    text-align: center;
}

.card img {
    border-radius: 6px;
    object-fit: fill;
    width: 100%;
    max-height: 170px;
}